.Home{
    padding:0;
    margin:0;
    height: 100%;
    width: 100vw;
    background-color: transparent;
   
}
html, body {
    height: 100%;
    width: 100%;
}

*{
    box-sizing: border-box;
}
body{
    padding:0;
    margin:0;
}


.Main{
    color:whitesmoke;
    z-index:-1;
    border-radius: 25px;
    max-width: 100vw;
    font-size: 25px;
    margin-top: 0.5%;
    display: flex;
    justify-content:center;
    backdrop-filter: blur(0.1rem);
    
}
.SText{
    font-size: 80px;
    margin-top: 200px;
    width: 50%;
    color:white;
    margin:auto;
    text-align: center;
    
}
.About{
   
    font-size: 1.3rem;
    padding: 20px;
    border-radius: 25px;
    width: 80%;
    margin: auto;
    margin-top: 100px;
    backdrop-filter: blur(10px);
    backdrop-filter: blur(0.1rem);
    

}

.first-section{
    width:100vw;
    height:min(100vh,900px);
    position: relative;
    margin:0;
    background-color: var(--card-bg);
    backdrop-filter: blur(20rem);
   
    display:flex;
    justify-content:center;
    align-items:flex-start;
    color:var(--secondary-text);
    text-shadow: 0 3px 5px rgb(214, 6, 121);
}
.Skills{
    background-color: var(--card-bg);
    box-shadow: rgba(0, 0, 0, 0.4) 0px 1px 4px;
    border-radius: 5px;
    padding: 20px;
    color:var(--primary-text);
    margin:auto;
    margin-top: 100px;
    width: min(1500px,80vw);
    box-sizing: border-box;
    position: relative;
    overflow: hidden;
    display:block;
    -webkit-backdrop-filter: blur(20px);
    backdrop-filter: blur(20px);
    width: min(800px,80vw);
    
}


.badge:hover{
    color:red;
}

.about-top{
    
    background-color: rgba(200,200,200,.15);
    padding:0;
    margin:0;
}
.About{
    display: flex;
    justify-content:center;
    background-color:rgba(0, 0, 0, 0.06);
    -webkit-backdrop-filter: blur(20px);
    backdrop-filter: blur(20px);
    box-shadow: rgba(0, 0, 0, 0.4) 0px 1px 4px;
    color:whitesmoke;
    text-align:center;
    border-radius: 5px;
    padding: 0px;
    padding-bottom: 20px;
    margin: 50px;
    margin: auto;
    margin-top: 100px;
    width: min(500px,80vw);
    display: block;
    margin-bottom: 20px;
    opacity: 0.8;
    transition: 0.15s;
    box-sizing: border-box;
    position: relative;
    overflow: hidden;
    backdrop-filter: blur(5px);
    font-size: 1rem;

}
.Typewriter__wrapper{
  color:var(--navtext-alt);  
}
.About ul{
    text-decoration: none;
    text-align: left;
    font-size:20px;
    
    justify-content:center;
}
.About li{
    font-weight: bold;
    color:var(--primary-text)
}
.text-shadow{
    text-shadow: 0 3px 5px whitesmoke;
}
@media(max-width:800px)
{
 .SText{
    width: 100%;
    display: block;
    font-size: 40px;
 }   
 .first-section{
    flex-direction: column;
    justify-content:center;
    align-items:center;
 }
}
.header{
    font-weight: bold;
    color:var(--primary-text );
    
}
 
.home-container{
    backdrop-filter: blur(2rem);
}

.main-container{
 
   width: fit-content;
   margin: auto;
}
.about-container{

    backdrop-filter: blur(1rem);
    width:fit-content;
    margin:auto;

}


.skills-container{
    backdrop-filter: blur(1rem);
    width:fit-content;
    margin:auto;
    display: block;
    
}

.Skills ul {
    text-decoration: none;
    text-align: center;
    font-size:20px;
    display:flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.Skills span{
    display:inline-block;
}
.Skills li{
    font-weight : bold;
    background-color:  rgb(0, 149, 230,1);
    border: rgba(255, 11, 141, 0.8) solid 3px;
    box-shadow: 1px 5px 10px rgba(255, 23, 147, 0.8);
    border-radius: 50%;
    height:300px;
    width:300px;
    display:flex;
    color:var(--primary-text );
    justify-content:center;
    align-items: center;
    margin:10px;
    overflow-wrap: break-word;
    transition:0.3s;
    justify-content: center;
    align-items: center;
  
}
.Skills li:active, .Skills li:focus{
    transform:rotateY(90deg);
    opacity:0.8;
    background-color:rgba(255, 23, 147, 0.8); 
    box-shadow: 10px 30px 40px rgba(255, 23, 147, 0.8);
}
.Skills h2{
    position: relative;
    top:-200px;
    right: 0;
    left: 0;
    z-index:-2;
    padding:0;
    margin:0;
    font-size: 0.8rem;
    overflow: break-word;
    width:200px;
    margin: auto;
}
.openMenu{
  width: 35px;
  height: 5px;
  background-color: var(--secondary-nav);
  margin: 6px 0;
  transition: .15s;
}
.menu-bar-icon {
    color:var(--secondary-nav)
}
.menu-bar-icon :hover ,.menu-bar-icon:active{
    opacity : 0.8;
    background-color:whitesmoke;
    
}
.about-content{
    text-align: left;
    margin: 1rem;
}

@media(max-width:800px)
{
 .SText{
    width: 100%;
    display: block;
    font-size: 40px;
 }   
 .first-section{
    flex-direction: column;
    justify-content:center;
    align-items:center;
 }
 .Skills li{
    width: 200px;
    height: 200px;
   
}
.Skills h2{
    font-size: 0.4rem;
    width:100px
}
 
}