.CardElement{
  
    color:black;
}
.CardHeader{
   
    color:whitesmoke;
    font-weight: 900;

}
a{
    background-color: rgba(0,0,0,0.15);
}
.card-a{
    
}
.Card{
    background-color:rgb(0, 90, 201,0.65);
    box-shadow: rgba(0, 0, 0, 0.4) 0px 1px 4px;
    border-radius: 10px;
    padding: 0px;
    
    width: min(800px,80vw);
    margin-bottom: 20px;
  ;
    transition: 0.15s;
    box-sizing: border-box;
    margin:4%;
    display: flex;
    flex-direction: column;
    align-items:center;
    z-index:1;
    -webkit-backdrop-filter: blur(1rem);
    backdrop-filter: blur(0.2em);
    margin:0;
    padding:1rem;
}
.CardImage{
    min-height: 200px;
    width: min(400px,50vw);
    padding: 20px;
}
.Card:hover{
    transform: scale(1.1);
    background-color:#ff3388d1;
    color:whitesmoke;

}

.card-a{
    text-decoration: none;
}
@media (max-width:600px)
{
    .Card{
        margin: auto;
        margin-top:10%;
        margin-bottom:10%;
        width: 90%;
    }
}
.card-content{
    font-weight: bold;
    color:rgb(216, 216, 216);
}
.Card a{
    color:var(--primary-color);
    
}
.card-content{
    text-align: center;
    display: flex;
    direction: row-reverse;
    justify-content:space-between;
    width:fit-content;
    font-size: 0.8rem;
    color:aliceblue;
}
html{

}
.card-image{
    flex-basis: 300px;
    flex-grow: 1;
    padding:1rem;
}
.card-text{
    padding:2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color:#e4e4e4;
    text-align: left;
}
.new-bg{
    background-color: rgba(255, 0, 115,0.65);
}
@media (max-width:600px)
{
    .Card{
        margin: auto;
        margin-top:10%;
